@import url(https://fonts.googleapis.com/css2?family=Montserrat:wght@300&family=Press+Start+2P&display=swap);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

:root {
  --primary: #030b16;
  --secondary: #f2f3fa;
  --tertiary: #62657a;
}

.App {
  text-align: center;
  font-family: "Montserrat", sans-serif;
  color: #f2f3fa;
  color: var(--secondary);
  background-color: #030b16;
  background-color: var(--primary);
  padding: 10%;
  /* padding-top: 5%; */
  background-image: url(/static/media/technology.f1335280.png);
  background-repeat: no-repeat;
  background-size: 100%;
}

.photo {
  width: 50%;
  border-radius: 50%;
}

.name {
  font-family: "Press Start 2P", sans-serif;
  font-size: 2em;
  margin-bottom: 0.25em;
}

.heading {
  font-family: "Press Start 2P", sans-serif;
  font-size: 1.5em;
  font-weight: lighter;
  padding: 0 1em;
}

.text {
  font-size: larger;
}

.justify {
  text-align: justify;
  text-justify: inter-word;
}

.left {
  text-align: left;
}

.right {
  text-align: right;
}

.section {
  margin-top: 5em;
}

.bubble {
  display: inline-flex;
  align-items: center;
  background-color: #62657a;
  background-color: var(--tertiary);
  color: #f2f3fa;
  color: var(--secondary);
  border-radius: 10px;
  padding: 0.5em 1em;
  margin: 0.3em;
}

@media only screen and (max-width: 600px) {
  .photo {
    width: 100%;
  }

  .App {
    padding: 10% 4%;
  }

  .heading {
    font-size: 1em;
  }
}

:root {
  --primary: #030b16;
  --secondary: #f2f3fa;
  --tertiary: #62657a;
}

.title,
.modal-title {
  font-size: 2em;
  font-weight: bold;
  color: #62657a;
  color: var(--tertiary);
}

.subtitle,
.modal-subtitle {
  font-size: 1.2em;
  margin-bottom: 1.5em;
  color: #62657a;
  color: var(--tertiary);
}

.modal-title,
.modal-subtitle {
  color: #f2f3fa;
  color: var(--secondary);
}

.logo {
  width: 100%;
}

.modal {
  margin: 10% auto;
  padding: 2em 3em 3em 3em;
  width: 50%;
  background-color: #62657a;
  background-color: var(--tertiary);
  color: #f2f3fa;
  color: var(--secondary);
  outline: 0;
}

.close-btn-container {
  text-align: right;
  color: #f2f3fa;
  color: var(--secondary);
}

.close-btn {
  cursor: pointer;
}

.go-to {
  color: #030b16;
}

.project-title {
  font-size: 1.4em;
  font-weight: bold;
  color: #62657a;
}

.project-date {
  margin: 0.2em;
  color: #62657a;
  font-size: 0.9em;
}

.project-description {
  margin-top: 1em;
  text-align: justify;
  text-justify: space-between;
}

